body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.formBox {
  margin-top: 90px;
  padding: 50px;
}
.formBox h1 {
  margin: 0;
  padding: 0;
  text-align: center;
  margin-bottom: 50px;
  text-transform: uppercase;
  font-size: 48px;
}
.inputBox {
  position: relative;
  box-sizing: border-box;
  margin-bottom: 50px;
}
.inputBox .inputText {
  position: absolute;
  font-size: 24px;
  line-height: 50px;
  transition: 0.5s;
  opacity: 0.5;
}
.inputBox .input {
  position: relative;
  width: 100%;
  height: 50px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 24px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}
.focus .inputText {
  transform: translateY(-30px);
  font-size: 18px;
  opacity: 1;
  color: #00bcd4;
}
textarea {
  height: 100px !important;
}
.button {
  width: 100%;
  height: 70px;
  border: none;
  outline: none;
  background: #03a9f4;
  color: #fff;
}
.cardBordercolor {
  border-width: 45rem;
}
.my-custom-scrollbar {
  position: relative;
  height: 300px;
  overflow: auto;
}
.table-wrapper-scroll-y {
  display: block;
}
.tableFixHead {
  overflow-y: auto;
  height: 100px;
}
.tableFixHead thead th {
  position: sticky;
  top: 0;
}
.navbar {
  background: #f0f0f0;
}
